import React from 'react';
import Box from '@mui/material/Box';
import { useTranslation } from 'react-i18next';
import List from '@mui/material/List';
import GitHubIcon from '@mui/icons-material/GitHub';
import CallIcon from '@mui/icons-material/Call';
import EmailIcon from '@mui/icons-material/Email';
import LinkedInIcon from '@mui/icons-material/LinkedIn';
import LocationOnIcon from '@mui/icons-material/LocationOn';
import { useThemeContext } from '../Burger/themeContext';
import themes from './../Burger/theme';
import Typography from '@mui/joy/Typography';
import TextField from '@mui/material/TextField';
import { DarkMode } from '@mui/icons-material';

const Contact = () => {
  const { t } = useTranslation();
  const {darkMode} = useThemeContext();

  const icons = (
    <List sx={{display:"flex", flexWrap:"wrap", width: '200px', justifyContent: 'space-between'}}>
        <GitHubIcon />
        <CallIcon />
        <EmailIcon />
        <LinkedInIcon />
    </List>
  )

  // const CustomTextField = (props) => {
  //   MuiOutlinedInput: {
  //     styleOverrides: {
  //       notchedOutline: {
  //         borderColor: 'var(--TextField-brandBorderColor)',
  //       },
  //       root: {
  //         [`&:hover .${outlinedInputClasses.notchedOutline}`]: {
  //           borderColor: 'var(--TextField-brandBorderHoverColor)',
  //         },
  //         [`&.Mui-focused .${outlinedInputClasses.notchedOutline}`]: {
  //           borderColor: 'var(--TextField-brandBorderFocusedColor)',
  //         },
  //       },
  //     },
  //   },
  // };

  const CustomTextField = (props) => {
    return (
      <TextField
        {...props}
        color={DarkMode ? 'primary' : 'textPrimary'}
      InputLabelProps={{
        style: {
          color: DarkMode ? themes.dark.text : themes.light.primaryColor,
        },
      }}
        sx={{
          '& .MuiOutlinedInput-root': {
            '& fieldset': {
              borderColor: 'var(--TextField-brandBorderColor)',
              color: DarkMode? themes.dark.primaryColor : themes.light.primaryColor,
            },
            '&:hover fieldset': {
              borderColor: 'var(--TextField-brandBorderHoverColor)',
            },
            '&.Mui-focused fieldset': {
              borderColor: 'var(--TextField-brandBorderFocusedColor)',
              color: DarkMode? themes.dark.primaryColor : themes.light.primaryColor,

            },
          },
        }}
      />
    );
  };

  return (
    <Box
      component="main"
      sx={{
        flexGrow: 1,
        p: 3,
        height: '100vh',
        overflow: 'auto',
        backgroundColor: darkMode ? themes.dark.backgroundColor : themes.light.backgroundColor,
        minWidth: '100%',
        color: darkMode ? 'white' : '#000000',
      }}
    >
      <Box sx={{ minHeight: '100vh', margin: '3%', bgcolor: '#171717', borderRadius: '10px', padding: '4%'}}>
          <Typography style={{ color: darkMode? themes.dark.text : themes.light.textColor, textAlign: 'center', marginBottom: '30px', fontSize: '2.6rem' }}>{t('CONTACT')}</Typography> <br></br><br></br>
          <Box sx={{display:'flex', flexWrap:'wrap', justifyContent:'space-around'}}>
          <Box sx={{margin: '3%', display: 'flex', flexDirection: 'column', justifyContent: 'space-between', width: '30%', height: '350px'}}>
          <CustomTextField
          required
          id="outlined-required"
          label="Nom"
        />
        <CustomTextField
          required
          id="outlined-required"
          label="Prénom"
        />
        <CustomTextField
          required
          id="outlined-required"
          label="Email"
        />
        <CustomTextField
          required
          id="outlined-multiline-static"
          label="Message"
          multiline
          rows={5}
        />
          </Box>


        <Box>
        <h2><LocationOnIcon></LocationOnIcon> EMPLACEMENT</h2><br></br>
        <p>Valbonne Sophia-Antipolis <br></br>06560</p><br></br><br></br>
        <h2>
            Mes Informations
        </h2>
        <br></br>
        {icons}
        </Box>
        </Box>
        </Box>
      </Box>
  );
};

export default Contact;

